import {Component} from 'react';

class List extends Component{
    render() {
        return (
            <div>
                <table class="table table-dark">
                <thead>
                <tr>
                <th scope="col">FirstName</th>
                <th scope="col">LastName</th>
                <th scope="col">Country</th>
                <th scope="col">Telephone</th>
                <th scope="col">STREET ADDRESS</th>
                <th scope="col"> OPERATIONS</th>
                </tr>
    
                    {
                        this.props.dir.map((item, index)=>(
                            <tr scope="col" key={index}>
                            <th scope="col">{item.fname}</th>
                            <th scope="col">{item.lname}</th> 
                            <th scope="col">{item.country} </th>
                            <th scope="col">{item.tel}</th>
                            <th scope="col">{item.sa}</th>
                                <span className="float-right">
                            <button
                                className="btn btn-success btn-sm mr-2"
                                onClick={
                                    (e) => this.props.view(item, e)
                                }>View</button>&nbsp;
                             <button
                                 className="btn btn-warning btn-sm mr-2"
                                 onClick={(e)=> this.props.edit(index, e)}
                             >Update</button>&nbsp;
                             <button
                                 className="btn btn-danger btn-sm"
                                 onClick={
                                     (e)=>this.props.delete(index, e)
                                 }
                             >Delete</button>&nbsp;
                        </span>
                        
                            </tr>
                        ))
                    }
                    </thead>
                </table>
            </div>
        )
    }
}

export default List;